        } else {
          this.getMasgg(resp.data.msg)
        }
      })
      this.printing.br_code = this.detailDTO.orderCode
      this.printing.dy_ks = this.resourcePatin.orgName// 
      this.printing.dy_ch = this.resourcePatin.bedNum // 
      this.printing.dy_dh = this.detailDTO.phone// 
      this.printing.dy_xm = this.detailDTO.patientName// 
      this.printing.dy_sfzh = ''// 
      this.printing.dy_lx = this.resourcePatin.serviceTypeName// 
      this.printing.dy_dj = this.resourcePatin.price// 
      this.printing.dy_ksr = this.resourcePatin.startDate.substring(0, 10)// 
      this.printing.dy_ze = this.resourceTransaction.money// 
      this.printing.dy_jsr = this.user.name// 
      // ----------
      // .
      var strmoney = this.padding(this.resourceTransaction.money, 5)
      let w = strmoney.charAt(strmoney.length - 5)// 
      let q = strmoney.charAt(strmoney.length - 4)// 
      let b = strmoney.charAt(strmoney.length - 3)// 
      let s = strmoney.charAt(strmoney.length - 2)// 
      let g = strmoney.charAt(strmoney.length - 1)// 
      for (let i = 0; i < 1; i++) {
        this.printing.w = this.convertCurrency(w) !== '' ? this.convertCurrency(w) : ''
        this.printing.q = this.convertCurrency(q) !== '' ? this.convertCurrency(q) : ''
        this.printing.b = this.convertCurrency(b) !== '' ? this.convertCurrency(b) : ''
        this.printing.s = this.convertCurrency(s) !== '' ? this.convertCurrency(s) : ''
        this.printing.g = this.convertCurrency(g) !== '' ? this.convertCurrency(g) : ''
      }
    },
    // 
    getSetSj () {
      var myDate = new Date()
      if (this.detailDTO.leaveDate === null) {
        this.getMasgg('')
        return
      }
      if (this.resourcePatin.orgName === undefined) {
        this.getMasgg('')
        this.resourcePatin.orgName = undefined
        return
      }
      this.sJDialogVisible = true
      this.printingSj.p_date_y = myDate.getFullYear()
      this.printingSj.p_date_m = myDate.getMonth() + 1
      this.printingSj.p_date_d = myDate.getDate()
      this.getRequest('/busi/baseinfo/docfile?id=2').then(resp => {
        if (resp && resp.status === 200) {
          this.printingSj.br_code = 'NO' + resp.data.msg.currentCode // 
        } else {
          this.getMasgg(resp.data.msg)
        }
      })
      this.printingSj.p_br_code = this.detailDTO.orderCode // 
      this.printingSj.p_dy_ks = this.resourcePatin.orgName // 
      this.printingSj.p_dy_ch = this.resourcePatin.bedNum // 
      this.printingSj.p_dy_xm = this.detailDTO.patientName // 
      this.printingSj.p_jsr = this.user.name // 
      // this.printingSj.p_dy_q=this.detailDTO.totalServiceAmount - this.leaveDTO.money;  //
      this.printingSj.p_dy_q = this.detailDTO.totalServiceAmount - this.leaveDTO.money // -
      this.printingSj.p_dy_fwsj1 = this.detailDTO.hospitalizedDate.substring(0, 10) // 
      this.printingSj.p_dy_fwsj2 = this.detailDTO.leaveDate.substring(0, 10) // 
      // -----
      // .
      var strmoney = this.padding(this.detailDTO.totalServiceAmount, 4)
      let q = strmoney.charAt(strmoney.length - 4)// 
      let b = strmoney.charAt(strmoney.length - 3)// 
      let s = strmoney.charAt(strmoney.length - 2)// 
      let g = strmoney.charAt(strmoney.length - 1)// 
      for (let i = 0; i < 1; i++) {
        this.printingSj.p_q = this.convertCurrency(q) !== '' ? this.convertCurrency(q) : ''
        this.printingSj.p_b = this.convertCurrency(b) !== '' ? this.convertCurrency(b) : ''
        this.printingSj.p_s = this.convertCurrency(s) !== '' ? this.convertCurrency(s) : ''
        this.printingSj.p_g = this.convertCurrency(g) !== '' ? this.convertCurrency(g) : ''
      }
    },
    // 
    getRenew () {
      var myDate = new Date()
      this.renew.xf_ymd = myDate.getFullYear() + '' + (myDate.getMonth() + 1) + '' + myDate.getDate() + ''
      this.getRequest('/busi/baseinfo/docfile?id=1').then(resp => {
        if (resp && resp.status === 200) {
          this.renew.xf_Rcode = resp.data.msg.currentCode// 
        } else {
          this.getMasgg(resp.data.msg)
        }
      })

      this.renew.xf_code = this.curRow.orderCode
      this.renew.xf_ks = this.curRow.orgName// 
      this.renew.xf_ch = this.resourcePatin.bedNum // 
      this.renew.xf_dh = this.chargeDTO.phone// 
      this.renew.xf_xm = this.chargeDTO.patientName// 
      this.renew.xf_sfzh = ''// 
      this.renew.xf_lx = this.curRow.serviceType// 
      this.renew.xf_dj = this.curRow.price// 
      this.renew.xf_ksr = this.chargeDTO.date.substring(0, 10)// 
      this.renew.xf_ze = this.chargeDTO.money// 
      this.renew.xf_jsr = this.user.name// 
      // ----------
      // .
      var strmoney = this.padding(this.chargeDTO.money, 5)
      let w = strmoney.charAt(strmoney.length - 5)// 
      let q = strmoney.charAt(strmoney.length - 4)// 
      let b = strmoney.charAt(strmoney.length - 3)// 
      let s = strmoney.charAt(strmoney.length - 2)// 
      let g = strmoney.charAt(strmoney.length - 1)// 
      for (let i = 0; i < 1; i++) {
        this.renew.xf_w = this.convertCurrency(w) !== '' ? this.convertCurrency(w) : ''
        this.renew.xf_q = this.convertCurrency(q) !== '' ? this.convertCurrency(q) : ''
        this.renew.xf_b = this.convertCurrency(b) !== '' ? this.convertCurrency(b) : ''
        this.renew.xf_s = this.convertCurrency(s) !== '' ? this.convertCurrency(s) : ''
        this.renew.xf_g = this.convertCurrency(g) !== '' ? this.convertCurrency(g) : ''
      }
    },
    // 
    convertCurrency (money) {
      // 
      var cnNums = new Array('', '', '', '', '', '', '', '', '', '')
      // 
      var cnIntRadice = new Array('', '', '', '')
      // 
      var cnIntUnits = new Array('', '', '', '')
      // 
      var cnDecUnits = new Array('', '', '', '')
      // 
      var cnInteger = ''
      // 
      var cnIntLast = ''
      // 
      var maxNum = 999999999999999.9999
      // 
      var integerNum
      // 
      var decimalNum
      // 
      var chineseStr = ''
      // 
      var parts
      if (money == '') { return '' }
      money = parseFloat(money)
      if (money >= maxNum) {
        // 
        return ''
      }
      if (money == 0) {
        chineseStr = cnNums[0] + cnIntLast + cnInteger
        return chineseStr
      }
      // 
      money = money.toString()
      if (money.indexOf('.') == -1) {
        integerNum = money
        decimalNum = ''
      } else {
        parts = money.split('.')
        integerNum = parts[0]
        decimalNum = parts[1].substr(0, 4)
      }
      // 
      if (parseInt(integerNum, 10) > 0) {
        var zeroCount = 0
        var IntLen = integerNum.length
        for (var i = 0; i < IntLen; i++) {
          var n = integerNum.substr(i, 1)
          var p = IntLen - i - 1
          var q = p / 4
          var m = p % 4
          if (n == '0') {
            zeroCount++
          } else {
            if (zeroCount > 0) {
              chineseStr += cnNums[0]
            }
            // 
            zeroCount = 0
            chineseStr += cnNums[parseInt(n)] + cnIntRadice[m]
          }
          if (m == 0 && zeroCount < 4) {
            chineseStr += cnIntUnits[q]
          }
        }
        chineseStr += cnIntLast
      }
      // 
      if (decimalNum != '') {
        var decLen = decimalNum.length
        for (var i = 0; i < decLen; i++) {
          var n = decimalNum.substr(i, 1)
          if (n != '0') {
            chineseStr += cnNums[Number(n)] + cnDecUnits[i]
          }
        }
      }
      if (chineseStr == '') {
        chineseStr += cnNums[0] + cnIntLast + cnInteger
      } else if (decimalNum == '') {
        chineseStr += cnInteger
      }
      return chineseStr
    },
    // 
    padding (num, length) {
      for (var len = (num + '').length; len < length; len = num.length) {
        num = '0' + num
      }
      return num
    },
    // 
    divPrint (id) {
      let subOutputRankPrint = document.getElementById(id)
      console.log(subOutputRankPrint.innerHTML)
      let newContent = subOutputRankPrint.innerHTML
      let oldContent = document.body.innerHTML
      document.body.innerHTML = newContent
      window.print()
      window.location.reload()
      document.body.innerHTML = oldContent
      return false
    },
    // 
    getMasgg (data) {
      this.$confirm(data, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.changeDialog = false
        this.searchPatientList()
      }).catch(() => {
      })
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    }
  }
}
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
  .labelstyle1 {
    width:80px;
    display: inline-block;
    border-bottom: 1px solid #000;
    text-align: center
  }
  .labelstyle{
    width:80px;
    display:inline-block;
    border-bottom:1px solid #fff;
    text-align:center;
    margin-top: -35px;
  }
  .labelstyle2 {
    width: 71px;
    display: inline-block;
    border-bottom: 1px solid #000;
    text-align: center
  }
  table {
    border-collapse: collapse;
  }
  th, td {
    padding: 0.5em 1em;
    font-size: 14px;
  }

  .el-table--striped .el-table__body tr.el-table__row--striped.current-row td, .el-table__body tr.current-row>td, .el-table__body tr.hover-row.current-row>td, .el-table__body tr.hover-row.el-table__row--striped.current-row>td, .el-table__body tr.hover-row.el-table__row--striped>td, .el-table__body tr.hover-row>td {
    background-color: #409effa1;
  }

</style>



