        date: ''
      },
      defaultProps: {
        children: 'children',
        label: 'label',
        id: 'id',
        isLeaf: 'isLeaf',
        disabled: 'disabled'
      },
      sexData: [
        {
          sex: '1',
          mame: ''
        }, {
          sex: '2',
          mame: ''
