  mounted: function () {

  },
  methods: {

    testDate () {
      console.log(this.hospitalizedInfo.hospitalizedTime)
    },

    number () {
      this.serviceDay = this.serviceDay.replace(/[^\.\d]/g, '')
      this.patient.phone = this.patient.phone.replace(/[^\.\d]/g, '')
      // this.transactionRecord.money = this.transactionRecord.money.replace(/[^\.\d]/g, '');
    },

    showEditEmpView () {
      this.dialogTitle = ''
    },
    // 
    showOrgTable () {
      this.orgdialogTitle = ''
      this.EdialogVisible = true
    },
    // 
    loadChild (node, resolve) {
      let id = (node.parent == null ? null : node.data.id)
      let resolve1 = resolve
      this.tableLoading = true
      this.postRequestJson('/busi/baseinfo/organization/list', {
        orgId: id,
        projectId: this.$store.state.user.projectId
      }).then(resp => {
        this.tableLoading = false
        if (resp && resp.status === 200) {
          const tdata = []
          for (let i = 0; i < resp.data.msg.result.length; i++) {
            tdata.push({
              id: resp.data.msg.result[i].id,
              label: resp.data.msg.result[i].name,
              children: [],
              isLeaf: resp.data.msg.result[i].isLeaf,
              disabled: !resp.data.msg.result[i].isLeaf
            })
          }
          resolve1(tdata)
        }
      })
    },
    // 
    showService () {
      this.SdialogTitle = ''
      this.SdialogVisible = true
      this.getServiceTypeload()
    },
    // 
    currentChange (currentChange) {
      this.currentPage = currentChange
      this.getServiceTypeload()
    },
    // 
    getServiceTypeload () {
      this.postRequestJson('/busi/baseinfo/caretype/list', {
        name: '',
        curPage: this.currentPage,
        projectId: this.$store.state.user.projectId
      }).then(resp => {
        this.tableLoading = false
        if (resp && resp.status === 200) {
          this.CareTypeList = resp.data.msg.result
          this.totalCount = resp.data.msg.totalSize
        } else {
          this.$confirm(resp.data.msg, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
          }).catch(() => {
          })
        }
      })
    },
    // 
    orgClick () {
      this.EdialogVisible = false
      let node = this.$refs.tree.getCheckedNodes(true, false)
      this.orgDataNode.orgName = node[0].label
      this.orgDataNode.orgId = node[0].id
    },
    // 
    orgNodeClick (data, nodes) {
      let target = []
      target.push(data)
      this.$refs.tree.setCheckedNodes(target, true)
    },
    // 
    cancelEidt () {
      this.dialogVisible = false
      this.EdialogVisible = false
    },
    // 
    SelectionChange (val) {
      this.resourceSelection = val
    },
    // 
    ServiceClick () {
      this.serviceType.serviceName = this.resourceSelection.name
      this.serviceType.price = this.resourceSelection.price
      this.serviceInfo.careTypeId = this.resourceSelection.id
      if (this.transactionRecord.money !== '') {
        this.serviceDay = this.transactionRecord.money / this.serviceType.price
      }
      this.SdialogVisible = false
    },
    // 
    moneyChange () {
      if (this.serviceType.price !== '') {
        this.serviceDay = this.transactionRecord.money / this.serviceType.price
      }
    },
    // 
    addPatient () {
      if (new Date(this.hospitalizedInfo.hospitalizedTime).getTime() >= new Date(this.serviceInfo.startTime).getTime()) {
        this.getMasgg('')
        return
      }
      const loading = this.$loading({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.hospitalizedInfo.hospitalizedTime += ' 00:00:00'
      let Record = {
        payMan: this.patient.name,
        money: this.transactionRecord.money,
        date: this.hospitalizedInfo.hospitalizedTime
      }
      let servicRecod = {
        bedNum: this.serviceInfo.bedNum,
        orgId: this.orgDataNode.orgId,
        careTypeId: this.serviceInfo.careTypeId,
        startTime: this.serviceInfo.startTime
      }

      this.postRequestJson('/busi/patient', {
        hospitalizedInfo: this.hospitalizedInfo,
        serviceInfo: servicRecod,
        patient: this.patient,
        transactionRecord: Record,
        projectId: this.$store.state.user.projectId
      }).then(resp => {
        loading.close()
        if (resp && resp.status === 200) {
          if (resp.data.code === '200') {
            this.patientCode = resp.data.msg.patientCode
            this.orderCode = resp.data.msg.orderCode
            this.getMasgg('')
          } else {
            this.getMasgg(resp.data.msg)
          }
        } else {
          this.getMasgg(resp.status + resp.data)
        }
      })
    },

    // 
    getMasgg (data) {
      this.$confirm(data, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
      }).catch(() => {
      })
    },
    clearAddView () {
      this.patient.code = ''
      this.patient.name = ''
      this.patient.address = ''
      this.patient.phone = ''
      this.patient.idcard = ''
      this.patient.sex = ''
      this.patient.linkman = ''
      this.serviceInfo.startTime = ''
      this.serviceInfo.bedNum = ''
      this.serviceInfo.careTypeId = ''
      this.serviceInfo.orgId = ''
      this.transactionRecord.money = 0
      this.transactionRecord.date = ''
      this.transactionRecord.payMan = ''
      this.hospitalizedInfo.hospitalizedTime = ''
      this.orgDataNode.orgName = ''
      this.orgDataNode.orgId = ''
      this.patientCode = ''
      this.orderCode = ''
      this.serviceType.serviceName = ''
      this.serviceType.price = ''
    },

    // 
    divPrint () {
      let subOutputRankPrint = document.getElementById('div_print')
      console.log(subOutputRankPrint.innerHTML)
      let newContent = subOutputRankPrint.innerHTML
      let oldContent = document.body.innerHTML
      document.body.innerHTML = newContent
      window.print()
      window.location.reload()
      document.body.innerHTML = oldContent
      return false
    },
    // 
    showAddEmpView () {
      if (this.patientCode === '') {
        this.getMasgg('')
        return
      }
      this.dialogTitle = ''
      this.dialogVisible = true
      this.getSetPatin()
    },
    // 
    getSetPatin () {
      var myDate = new Date()
      this.printing.y_m_d = myDate.getFullYear() + '' + (myDate.getMonth() + 1) + '' + myDate.getDate() + ''
      this.getRequest('/busi/baseinfo/docfile?id=1').then(resp => {
        if (resp && resp.status === 200) {
          this.printing.dy_code = resp.data.msg.currentCode
        } else {
          this.getMasgg(resp.data.msg)
        }
      })
      this.printing.br_code = this.orderCode
      this.printing.dy_ks = this.orgDataNode.orgName
      this.printing.dy_ch = this.serviceInfo.bedNum
      this.printing.dy_dh = this.patient.phone
      this.printing.dy_xm = this.patient.name
      this.printing.dy_sfzh = this.patient.idcard
      this.printing.dy_lx = this.serviceType.serviceName
      this.printing.dy_dj = this.serviceType.price
      this.printing.dy_ksr = this.serviceInfo.startTime.substring(0, 10)
      this.printing.dy_ze = this.transactionRecord.money
      this.printing.dy_jsr = this.user.name
      // ----------
      // .
      var strmoney = this.padding(this.transactionRecord.money, 5)
      let w = strmoney.charAt(strmoney.length - 5)// 
      let q = strmoney.charAt(strmoney.length - 4)// 
      let b = strmoney.charAt(strmoney.length - 3)// 
      let s = strmoney.charAt(strmoney.length - 2)// 
      let g = strmoney.charAt(strmoney.length - 1)// 
      for (let i = 0; i < 1; i++) {
        this.printing.w = this.convertCurrency(w) != '' ? this.convertCurrency(w) : ''
        this.printing.q = this.convertCurrency(q) != '' ? this.convertCurrency(q) : ''
        this.printing.b = this.convertCurrency(b) != '' ? this.convertCurrency(b) : ''
        this.printing.s = this.convertCurrency(s) != '' ? this.convertCurrency(s) : ''
        this.printing.g = this.convertCurrency(g) != '' ? this.convertCurrency(g) : ''
      }
    },
    // 
    convertCurrency (money) {
      // 
      var cnNums = new Array('', '', '', '', '', '', '', '', '', '')
      // 
      var cnIntRadice = new Array('', '', '', '')
      // 
      var cnIntUnits = new Array('', '', '', '')
      // 
      var cnDecUnits = new Array('', '', '', '')
      // 
      var cnInteger = ''
      // 
      var cnIntLast = ''
      // 
      var maxNum = 999999999999999.9999
      // 
      var integerNum
      // 
      var decimalNum
      // 
      var chineseStr = ''
      // 
      var parts
      if (money == '') {
        return ''
      }
      money = parseFloat(money)
      if (money >= maxNum) {
        // 
        return ''
      }
      if (money == 0) {
        chineseStr = cnNums[0] + cnIntLast + cnInteger
        return chineseStr
      }
      // 
      money = money.toString()
      if (money.indexOf('.') == -1) {
        integerNum = money
        decimalNum = ''
      } else {
        parts = money.split('.')
        integerNum = parts[0]
        decimalNum = parts[1].substr(0, 4)
      }
      // 
      if (parseInt(integerNum, 10) > 0) {
        var zeroCount = 0
        var IntLen = integerNum.length
        for (var i = 0; i < IntLen; i++) {
          var n = integerNum.substr(i, 1)
          var p = IntLen - i - 1
          var q = p / 4
          var m = p % 4
          if (n == '0') {
            zeroCount++
          } else {
            if (zeroCount > 0) {
              chineseStr += cnNums[0]
            }
            // 
            zeroCount = 0
            chineseStr += cnNums[parseInt(n)] + cnIntRadice[m]
          }
          if (m == 0 && zeroCount < 4) {
            chineseStr += cnIntUnits[q]
          }
        }
        chineseStr += cnIntLast
      }
      // 
      if (decimalNum != '') {
        var decLen = decimalNum.length
        for (var i = 0; i < decLen; i++) {
          var n = decimalNum.substr(i, 1)
          if (n != '0') {
            chineseStr += cnNums[Number(n)] + cnDecUnits[i]
          }
        }
      }
      if (chineseStr == '') {
        chineseStr += cnNums[0] + cnIntLast + cnInteger
      } else if (decimalNum == '') {
        chineseStr += cnInteger
      }
      return chineseStr
    },
    // 
    padding (num, length) {
      for (var len = (num + '').length; len < length; len = num.length) {
        num = '0' + num
      }
      return num
    }

  },
  computed: {
    user () {
      return this.$store.state.user
    },
    routes () {
      return this.$store.state.routes
    }
  }
}
</script>

<style scoped>
  .labelstyle {
    width: 71px;
    display: inline-block;
    border-bottom: 1px solid #000;
    text-align: center
  }

  table {
    border-collapse: collapse;
  }

  th, td {
    padding: 0.5em 1em;
    font-size: 14px;
  }
</style>



